.calc-container{
    display: flex;
    flex-grow: 1;
    flex-direction: row;
    margin: 20px;
}

.enclosure{
    width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.calc-container{
    width: 100%;
}

.title{
    font-size: 20px;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
}

.input-output-side{
    display: flex;
    flex-direction: 2;
    flex-direction: column;
    padding: 10px;
}

.numInput{
    margin: 5px;
    border-radius: 5px;
    border: solid #000 2px;
    width: 100%;
}

.answer{
    margin: 5px;
    border-radius: 5px;
    border: solid #000 2px;
    width: 100%;
    height: 30px;
}

.operator-submit-side{
    display: flex;
    flex-direction: 3;
    flex-direction: column;
    padding: 10px;
}

.racks{
    width: 100%;
    height: 33%;
    margin: 2px;
    display: flex;
    flex-direction: row;
}

.racksv2{
    flex-direction: column;
}

.small-screen{
    margin: 5px;
    border-radius: 5px;
    border: solid #000 2px;
    width: 100%;
}

.button-grid{
    display: flex;
    margin: 2px;
    text-align: center;
    width: 50%;
    justify-content: center;

    border: 0;
    color: white;
    background: none;
    border-radius: 0px;
    background-color: #1A73E8;
    transition-duration: 0.5s;
    box-shadow: 0 2px #555;
}

.button-grid:hover{
    background-color: #0E53ff;
}

.controls{
    width: 100%;
    margin: 6px;
    display: flex;
    flex-direction: row;
}

.v2{
    width: 100%
}
